import { Board, Cell, DeadZone } from './Board';
import { Player, PlayerType } from './Player';
import './Piece';
import { Lion } from './Piece';

export class Game {
    private selectedCell: Cell;
    private turn = 0;
    private currentPlayer: Player;
    private gameInfoEl = document.querySelector('.alert');
    // enumÎèÑ ÏÇ¨Ïö©Í∞ÄÎä•ÌïòÏßÄÎßå, Ìé∏ÏùòÏÉÅ union ÌÉÄÏûÖÏúºÎ°ú ÏûëÏÑ±, default value Îäî started
    private state: 'STARTED' | 'END' = 'STARTED';

    readonly upperPlayer = new Player(PlayerType.UPPER);
    readonly lowerPlayer = new Player(PlayerType.LOWER);

    //boardÎäî Í≥ÑÏÜç Ïì∞Ïù¥Î©¥ÏÑú, ÏûëÏóÖÏùÑ Í≥†Ï†ïÌïú ÏÉÅÌÉúÏù¥ÎØÄÎ°ú, readonly ÌÉÄÏûÖ
    readonly board = new Board(this.upperPlayer, this.lowerPlayer);
    readonly upperDeadZone = new DeadZone('upper');
    readonly lowerDeadZone = new DeadZone('lower');

    constructor() {
        console.log(this.upperPlayer);
        console.log(this.lowerPlayer);
        const boardContainer = document.querySelector('.board-container');
        // console.log(boardContainer.firstChild); //textÏ†úÍ±∞ÌïúÌõÑ childappendÌï¥Ï§ÑÍ≤É
        boardContainer.firstChild.remove();
        boardContainer.appendChild(this.board._el);

        // ÏãúÏûëÏùÄ Ìï≠ÏÉÅ upper playerÍ∞Ä ÌïòÎèÑÎ°ù ÏÑ§Ï†ï
        this.currentPlayer = this.upperPlayer;

        this.board.render();
        this.renderInfo();

        //event Ï≤òÎ¶¨
        this.board._el.addEventListener('click', (e) => {
            if (this.state === 'END') {
                return false;
            }
            //type guard: HTML elementÎßå Í∞ÄÏ†∏Ïò¥
            if (e.target instanceof HTMLElement) {
                let cellEl: HTMLElement;
                // ÏöîÏÜåÏùò ÌÅ¥ÎûòÏä§Î™ÖÏóê cellÏù¥ ÏûàÎäî ÏöîÏÜåÏôÄ, pieceÍ∞ÄÏûàÎäî Î∂ÄÎ™®ÏöîÏÜåÎ•º ÏÑ†ÌÉùÌï¥ÏÑú cellElÏóê Ï†ÄÏû•ÌïòÍ≥† ÎÇòÎ®∏ÏßÄ cellÏùÄ falseÎ°ú Î¨¥Ïãú
                if (e.target.classList.contains('cell')) {
                    cellEl = e.target;
                } else if (e.target.classList.contains('piece')) {
                    cellEl = e.target.parentElement;
                } else {
                    return false;
                }

                //boardÏóêÏÑú mappingÌï¥Ï§Ä HTML elementÎ•º Í∞ÄÏßÄÍ≥† ÏÑ†ÌÉùÎêú peiceÎ•º Í∞ÄÏ†∏Ïò¥
                const cell = this.board.map.get(cellEl);

                if (this.isCurrentUserPiece(cell)) {
                    this.select(cell);
                    return false;
                }
                //ÏûêÍ∏∞ÎßêÏùÑ ÏÑ†ÌÉùÌïú ÏÉÅÌÉúÎùºÎ©¥ ÏõÄÏßÅÏù¥Í≥†, ÌÑ¥ÏùÄ Î∞îÍøîÏ§å
                if (this.selectedCell) {
                    this.move(cell);
                    this.changeTurn();
                } else if (!this.selectedCell) {
                    return false;
                } else {
                    alert(`ÌòÑÏû¨ Ïú†Ï†ÄÎäî ${this.currentPlayer.type} ÏûÖÎãàÎã§.`);
                    return false;
                }
            }
        });
    }
    move(cell: Cell) {
        this.selectedCell.deactive();
        // selectedCell ÏùÑ cellÎ°ú Ïù¥ÎèôÌïòÎäîÎç∞, getKilledÎ•º ÌñàÏùÑÎïå ÏûàÏúºÎ©¥ Í∑∏Í±∏ Í∞ÄÏ†∏ÏôÄÏÑú Î∞îÍøîÏ§ÄÎã§.
        const killed = this.selectedCell.getPiece().move(this.selectedCell, cell).getKilled();
        this.selectedCell = cell;

        if (killed) {
            if (killed.ownerType === PlayerType.UPPER) {
                this.lowerDeadZone.put(killed);
            } else {
                this.upperDeadZone.put(killed);
            }
            if (killed instanceof Lion) {
                this.state = 'END';
            }
        }
    }
    // ÌòÑÏû¨ turnÏùò playerÏôÄ ÏùºÏπòÌïòÎ©¥ÏÑú, cell, piece Ï†ïÎ≥¥Í∞Ä ÏûàÏùÑÍ≤ΩÏö∞ trueÎ∞òÌôò
    isCurrentUserPiece(cell: Cell) {
        //!= ÏùÑ ÏÇ¨Ïö©ÌïúÏù¥Ïú†: ÎπÑÏñ¥ÏûàÎäîÍ≤ΩÏö∞(undefined)Î•º Í≥†Î†§Ìï¥ÏÑú
        return cell != null && cell.getPiece() != null && cell.getPiece().ownerType === this.currentPlayer.type;
    }
    select(cell: Cell) {
        //ÏÑ†ÌÉùÌïú pieceÍ∞Ä ÎπÑÏñ¥ÏûàÏúºÎ©¥ return
        if (cell.getPiece() == null) {
            return;
        }
        // ÏûêÍ∏∞ ÏûêÏã†Ïùò ÎßêÏùÑ ÏÑ†ÌÉùÌïúÍ≤å ÏïÑÎãàÎùºÎ©¥ return
        if (cell.getPiece().ownerType !== this.currentPlayer.type) {
            return;
        }

        //Ï¶â ÏûêÍ∏∞Í∞Ä ÏÑ†ÌÉùÌïú ÎßêÏù¥ ÎπÑÏñ¥ÏûàÏßÄÏïäÍ≥†, ÏûêÏã†Ïùò ÎßêÏù¥Ïñ¥ÏïºÏßÄÎßå ÏõÄÏßÅÏòÄÏùÑÎïå Î∞òÏùë
        if (this.selectedCell) {
            this.selectedCell.deactive();
            this.selectedCell.render();
        }
        this.selectedCell = cell;
        this.selectedCell.active();
        this.selectedCell.render();
    }
    //ÌÑ¥Ïù¥ Í≥ÑÏÜç ÏûàÎäî Í≤ΩÏö∞ÏóêÎäî parameterÍ∞Ä Ï†ÑÎã¨Ïù¥ ÏïàÎêòÎãàÍπå, ?Î•º Î∂ôÏó¨ÏÑú ÏÑ†ÌÉùÏ†ÅÏúºÎ°ú Îß§Í∞úÎ≥ÄÏàòÎ•º Î∞õÎäîÎã§.
    renderInfo(extraMessgae?: string) {
        this.gameInfoEl.innerHTML = `#${this.turn}ÌÑ¥ ${this.currentPlayer.type} Ï∞®Î°Ä ${extraMessgae ? '| ' + extraMessgae : ''}`;
    }
    changeTurn() {
        this.selectedCell.deactive();
        this.selectedCell = null;

        if (this.state === 'END') {
            this.renderInfo('END!');
        } else {
            this.turn += 1;
            this.currentPlayer = this.currentPlayer === this.lowerPlayer ? this.upperPlayer : this.lowerPlayer;
            this.renderInfo();
        }
        //Îß§ ÌÑ¥Ïù¥ ÏßÄÎÇ†ÎïåÎßàÎã§, ÌôîÎ©¥Ïóê Îã§Ïãú Í≤∞Í≥ºÍ∞Ä Í∑∏Î†§ÏßÄÎèÑÎ°ù ÌïúÎã§.
        this.board.render();
    }
}
